import teal from '@material-ui/core/colors/teal';
import grey from '@material-ui/core/colors/grey';

const themeDefaults = {
  bodyColor: {
    dark: grey['A400'],
    light: grey[50],
  },
  palette: {
    primary: teal,
    type: 'light'
  },
  drawerWidth: 240,
}

export default themeDefaults



